@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

.App {
  font-family: sans-serif;
  text-align: center;
  width: 100%;
}

div.App.dark {
  background-color: #373737;
}

div.containerPrincipal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

div.lineaSuperior {
  width: 1440px;
  height: 6px;
  left: 0px;
  top: 0px;
  background: #6809e1;
  padding-bottom: 14px;
}

div.headerContenedor {
  display: flex;
  flex-direction: column;
  width: 900px;
  height: 587px;
  border: 1px solid #e8e8e8;
  margin-top: 4px;
}

.dark h1.tituloResultados {
  color: #ffffff;
}

header.contenedorBusqueda {
  display: flex;
  padding: 65.89px 165px 0 164px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1.tituloPrincipal {
  width: 571px;
  font-weight: 400;
  font-size: 36px;
  text-align: center;
  color: #572ee5;
  margin-bottom: 14px;
}

.dark h1.tituloPrincipal {
  color: #ffffff;
}

div.barraDeBusqueda {
  display: flex;
  width: 542px;
  height: 52px;
}

input {
  width: 100%;
  font-weight: 200;
  font-size: 18px;
  line-height: 21px;
  border-radius: 61px 0 0 61px;
  padding-left: 25px;
  border: 1px solid #121212;
}

.dark input {
  background-color: #373737;
  border: 1px solid #ffffff;
  color: #ffffff;
}

button {
  width: 86px;
  background: #572ee5;
  border: 1px solid #000000;
  box-sizing: border-box;
  border-radius: 0px 61px 61px 0px;
  cursor: pointer;
}

.dark button {
  border: 1px solid #ffffff;
}

img.iconoLupa {
  margin: 16px 36px 16px 30px;
}

.listaItems {
  position: absolute;
  /*visibility: hidden;*/
  text-align: left;
  margin-top: 52px;

  cursor: pointer;
}

.listaItemsVisible {
  visibility: visible;
}

.listaItemsInvisible {
  display: none;
}

ul {
  list-style: none;
  font-weight: 200;
  font-size: 18px;
  line-height: 36px;
  width: 455px;
  padding-left: 25px;
  background-color: #ffffff;
}

li:hover {
  background-color: rgba(214, 209, 209, 0.616);
  width: 100%;
}

nav.containerNavbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 22px 68px 0 79px;
}

button.boton {
  width: 148px;
  height: 43px;
  border: 1px solid #6809e1;
  border-radius: 61px;
  color: #6809e1;
  background: #ffffff;
  font-weight: 700;
  font-size: 14px;
  cursor: pointer;
}

.dark button.boton {
  border: 1px solid #ffffff;
  background: #373737;
  color: #ffffff;
}

div.contenedorResultados {
  background-color: #e7e7e7;
  display: flex;
  flex-wrap: wrap;
  width: 900px;
  justify-content: center;
  padding-right: 28px;
  padding-top: 55px;
}

.dark div.contenedorResultados {
  background-color: #212121;
}

.contedorMensajeBusqueda {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tituloResultados {
  margin: 79px 0 31px 0;
  font-weight: 400;
  font-size: 24px;
  color: #572ee5;
}

.tituloBusqueda {
  margin: 120px 0 120px 0;
  font-weight: 400;
  font-size: 24px;
  color: #572ee5;
}

.dark .tituloBusqueda {
  color: #ffffff;
}

.textoError {
  margin: 10px 0 100px 0;
  font-weight: 400;
  font-size: 24px;
  color: #572ee5;
}

.dark .textoError {
  color: #ffffff;
}

div.contenedorImagenes {
  /*width: 900px;*/
  display: flex;
  justify-content: center;
}

.dark div.contenedorImagenes {
  background-color: #212121;
}

img.imagenesGifs {
  width: 250px;
  height: 250px;
  margin-left: 28px;
  margin-bottom: 41px;
  border: 1px solid #bbbbbb;
  box-sizing: border-box;
}

